body
{
	margin: 0;
	padding: 0;
	min-height: 100vh;
	font-family: 'PT Sans Narrow', sans-serif;
}

.note
{
	opacity: 0;
	position: absolute;
	left: 100%;
	top: 60%;
	transform: translateY(-50%);
	animation: fadeIn 1s linear forwards;
	animation-delay: 6s;
	z-index: 100;
}
section {
  margin: 0 120px;
}

header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 50px;
}

header div {
  margin-left: auto;
  font-size: 20px;
}

header ul li {
  display: inline-block;
  margin: 0 20px;
}


#main {
  margin: 0 120px;
  margin-top: 200px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

#main .main-text {
  font-size: 45px;
  font-weight: 700;
  line-height: 1.5;
}

span {
  border-bottom: 2px solid #3ff1aa;
}
